import React from 'react';
import '../styles/appleItem.scss';

class AppleItem extends React.Component {

    shouldComponentUpdate(nextProps) {
        return nextProps.state != this.props.state;
    }

    render() {
        let { apple, eatApple } = this.props;

        return (
            <div className="appleItem">
                <div className="apple"><img src={require('../images/apple.png')} alt=""/></div>
                <div className="info">
                    <div className="name"> - { apple.id }</div>
                    <div className="weight">{ apple.weight }</div>
                </div>
                <div className="btn-div">
                    <button onClick={eatApple.bind(this, apple.id)}>  </button>
                </div>
            </div>
        );
    }
}

AppleItem.propTypes = {
    eatApple: React.PropTypes.func.isRequired,   // bindActionCreatorsdispatch(action)
    apple: React.PropTypes.object.isRequired     // 
};


export default AppleItem;


