import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import actions from '../actions/appleAction';
import AppleItem from './AppleItem';
import '../styles/appleBasket.scss';

class AppleBusket extends React.Component {

    /**  */
    calculateStatus(){
        let status = {
            appleNow: {
                quantity: 0,
                weight: 0
            },
            appleEaten: {
                quantity: 0,
                weight: 0
            }
        };
        this.props.appleBasket.apples.forEach(apple => {
            let selector = apple.isEaten ? 'appleEaten':'appleNow';
                status[selector].quantity ++;
                status[selector].weight += apple.weight;
        });
        return status;
    }

    /** */
    getAppleItem(apples) {
        let data = [];
        apples.forEach(apple => {
            if (!apple.isEaten) {
                data.push( <AppleItem apple={apple} eatApple={this.props.actions.eatApple} key={apple.id}/> )
            }
        });

        if(!data.length) data.push(<div className="empty-tip" key="empty"></div>);

        return data;
    }

    render(){

        let { appleBasket, actions  } = this.props;
        let { apples, isPicking} = appleBasket;
        let status = this.calculateStatus();
        let {
            appleNow: {quantity:notEatenQuantity,weight:notEatenWeight},
            appleEaten: {quantity:EatenQuantity,weight:EatenWeight}
        } = status;


        return (
            <div className="appleBusket">
                <div className="title"></div>

                <div className="stats">
                    <div className="section">
                        <div className="head"></div>
                        <div className="content">{notEatenQuantity}{notEatenWeight}
                        </div>
                    </div>
                    <div className="section">
                        <div className="head"></div>
                        <div className="content">{EatenQuantity}{EatenWeight}</div>
                    </div>
                </div>

                <div className="appleList">
                    { this.getAppleItem(apples) }
                </div>

                <div className="btn-div">
                    <button  className={isPicking ? 'disabled' : ''}  onClick={actions.pickApple} ></button>
                </div>
            </div>
        );
    }
}



/**
 * state: state.appleBasket
 * storestatereducer
 * state.appleBasketreducer,state:state
 *
 * reducerreducersindex.jscombineReducers
 * state.appleBasketmany-react-demotodo4
 */
const mapStateToProps = state => ({
    appleBasket: state.appleBasket
});

const mapDispatchToProps = dispatch => ({
    actions: bindActionCreators(actions, dispatch)
});

 export default connect(mapStateToProps, mapDispatchToProps)(AppleBusket);


