//import ajax from '../services/ajax'; // ajax 
import $ from 'jquery';

//action
const prefix = 'apple/';

let actions = {

    /**
     *  ES5function
     *  Action Creator
     *  dispatchaction, 
     *  redux-thunkdispatch
     *  dispatchcreateStore
     *
     *  import { createStore, applyMiddleware  } from 'redux';
     *  const store = createStore(reducer, applyMiddleware(thunk));
     *  import thunk from 'redux-thunk';
     *
     *  const store = createStore(reducer);
     */
    pickApple: function() {

        return function(dispatch, getState) {
            /** thunk,  */
            if (getState().appleBasket.isPicking){
                return;
            }

            /**  */
            dispatch(actions.beginPickApple());

            $.ajax({
                url: 'https://hacker-news.firebaseio.com/v0/jobstories.json',
                method: 'GET'
            }).done(data => {
                /** urlhackernewsapi, mock */
                let weight = Math.floor(200 + Math.random()*50);
                dispatch(actions.donePickApple(weight));
            }).fail(xhr => {
                dispatch(actions.failPickApple(xhr.responseText));
            })
        }
    },

    beginPickApple: () => ({
        type: 'apple/BEGIN_PICK_APPLE'
    }),

    donePickApple: appleWeight => ({
        type: 'apple/DONE_PICK_APPLE',
        payload: appleWeight
    }),

    failPickApple: errMsg => ({
        type: 'apple/FAIL_PICK_APPLE',
        payload: new Error(errMsg),
        error: true
    }),

    eatApple: appleId => ({
        type: 'apple/EAT_APPLE',
        payload: appleId
    })

};

export default actions;


